const EMPTY_FIELD = '  '
const KIRILICA = '   '

export const loginValidation = {
    required: EMPTY_FIELD,
    validate: (value) => {
        if (value.match(/[--]/)) {
            return KIRILICA
        }
        return true
    }
}

export const passwordValidation = {
    required: EMPTY_FIELD,
    validate: (value) => {
        if (value.length < 6) {
            return '    5 '
        }
        if (value.match(/[--]/)) {
            return KIRILICA
        }
        return true
    }
}